@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;500;700&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

html {
  font-family: "Noto Sans JP", sans-serif;
}

body {
  height: 100vh;
}

.container {
  display: flex;
}

@media screen and (max-width: 1000px) {
  .tasks {
    padding: 20px !important;
  }

  .add-task {
    padding: 50px 20px 20px !important;
  }
}

@media screen and (max-width: 840px) {
  .container {
    flex-direction: column;
  }

  .container .add-task,
  .container .tasks {
    width: 100%;
    height: auto;
  }
}

@media screen and (max-width: 500px) {
  .pop-up {
    width: 80% !important;
  }
}

.Header_header__1dh2R {
  display: flex;
  grid-gap: 10px;
  gap: 10px;
  place-items: center;
  padding: 20px;
  height: 80px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.205);
  -webkit-transform: scale(1);
          transform: scale(1);
}

.Header_header__title__xDWVv,
.Header_header__icon__3zyTF {
  font-size: 25px;
  color: #ff4545;
}

.Header_header__icon__3zyTF {
  width: 40px;
}

.pop-up {
  opacity: 0;
  visibility: hidden;
  z-index: 1000;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%) scale(0.9);
          transform: translate(-50%, -50%) scale(0.9);
  width: 400px;
  background-color: white;
  padding: 25px;
  border-radius: 5px;
  text-align: center;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.137);
  transition: all 200ms ease;
}

.pop-up-show {
  opacity: 1;
  -webkit-transform: translate(-50%, -50%) scale(1);
          transform: translate(-50%, -50%) scale(1);
  visibility: visible;
}

.pop-up__title {
  font-size: 20px;
  margin-bottom: 20px;
  font-weight: 500;
}

.pop-up__input {
  width: 100%;
  margin-top: 25px;
  padding: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.233);
  border-radius: 5px;
  border: none;
}

.pop-up__content {
  width: 100%;
  height: 100px;
  margin-top: 15px;
  padding: 8px;
  border-radius: 5px;
  border: none;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.233);
  resize: none;
}

.pop-up__content:focus,
.pop-up__input:focus {
  outline: none;
}

.pop-up__buttons {
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 10px;
  gap: 10px;
  margin-top: 25px;
}

.pop-up__button {
  color: white;
  width: 120px;
  height: 40px;
  border-radius: 5px;
  font-weight: bold;
  font-size: 18px;
  border: none;
  cursor: pointer;
  transition: all 200ms ease;
}

.confirm {
  background-color: #59b637;
}

.cancel {
  background-color: #ff4545;
}

.confirm:hover {
  background-color: #5aad3c;
}

.cancel:hover {
  background-color: #dd3d3d;
}

.over-lay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.233);
  opacity: 0;
  visibility: hidden;
  z-index: 999;
  -webkit-transform: scale(1);
          transform: scale(1);
  transition: all 150ms ease;
}

.show {
  visibility: visible;
  opacity: 1;
}

.drop-down {
  position: relative;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  z-index: 100;
  -webkit-transform: scale(1);
          transform: scale(1);
}

.drop-down__change {
  display: flex;
  justify-content: space-between;
  padding: 8px;
  width: 130px;
  border: none;
  border-radius: 5px;
  background-color: white;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.137);
  cursor: pointer;
}

.drop-down__container {
  position: absolute;
  top: 40px;
  width: 100%;
  background-color: white;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.137);
  border-radius: 5px;
  padding: 10px;
  flex-direction: column;
  grid-gap: 5px;
  gap: 5px;
  z-index: 999;
  display: none;
}

.drop-down__container.show {
  display: flex;
}

.drop-down__item {
  padding: 10px;
  font-size: 14px;
  border-radius: 4px;
  cursor: pointer;
}

.drop-down__item:hover {
  background-color: rgba(0, 0, 0, 0.05);
}

.task {
  position: relative;
  background-color: white;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.158);
  padding: 20px 20px 20px 40px;
  border-radius: 8px;
}

.task-line {
  content: "";
  position: absolute;
  left: 12px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 5px;
  height: 80%;
  border-radius: 50px;
  transition: all 200ms ease;
}

.task del {
  color: rgba(0, 0, 0, 0.486);
}

.done-true {
  background-color: #69ff34;
}

.done-false {
  background-color: #ff4545;
}

.task__title {
  word-wrap: break-word;
  font-size: 20px;
  font-weight: 500;
}

.task__content {
  margin-top: 10px;
  word-wrap: break-word;
  font-size: 14px;
  font-weight: 300;
}

.task__actions {
  position: absolute;
  top: 10px;
  right: 10px;
  display: flex;
  grid-gap: 10px;
  gap: 10px;
  cursor: pointer;
}

.ball {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: none;
  cursor: pointer;
}

.task__remove {
  background-color: #ff4545;
}

.task__edit {
  background-color: #f0dc2a;
}

.task__done {
  background-color: #69ff34;
}

.tasks {
  width: 50%;
  height: calc(100vh - 80px);
  padding: 50px;
}

.tasks__title {
  display: flex;
  place-items: center;
  grid-gap: 10px;
  gap: 10px;
}

.tasks__title__text {
  font-size: 18px;
  font-weight: 500;
}

.tasks__filter {
  display: flex;
  justify-content: space-between;
  margin-top: 25px;
}

.btn-clear {
  width: 130px;
  background-color: white;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.137);
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.tasks__container {
  display: flex;
  height: 80%;
  flex-direction: column;
  overflow-x: hidden;
  overflow-y: auto;
  padding: 20px;
}

.tasks__container::-webkit-scrollbar {
  width: 10px;
}

.tasks__container::-webkit-scrollbar-thumb {
  width: 5px;
  background-color: rgb(223, 223, 223);
  border-radius: 50px;
}

.nothing {
  font-weight: 300;
  margin-top: 20px;
}

.add-task {
  background-color: #ff4545;
  width: 50%;
  height: calc(100vh - 80px);
  padding: 100px 50px 50px 50px;
}

.add-task__title {
  display: flex;
  grid-gap: 10px;
  gap: 10px;
  place-items: center;
}

.add-task__title__text {
  font-size: 18px;
  font-weight: 500;
  color: white;
}

.add-task__title__icon {
  width: 25px;
}

.add-task__form {
  display: flex;
  flex-direction: column;
  grid-gap: 20px;
  gap: 20px;
  margin-top: 15px;
}

.input {
  width: 100%;
  border: none;
  outline: none;
  padding: 10px;
  border-radius: 5px;
}

.add-task__count {
  font-size: 12px;
  text-align: right;
  color: rgba(255, 255, 255, 0.74);
}

.add-task__form__content {
  resize: none;
  height: 120px;
}

.add-task__form__submit {
  width: 250px;
  padding: 8px;
  background-color: transparent;
  border: 2px solid white;
  border-radius: 5px;
  color: white;
  cursor: pointer;
  margin-top: 10px;
  margin-inline: auto;
}

